<template>
  <div class="add-blog">
    <h2>添加博客</h2>
    <form v-if="!submited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required>
      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>
      <div class="checkboxes">
        <label >Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories">
        <label >React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories">
        <label >Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">
        <label >Angular.js</label>
        <input type="checkbox" value="Angular.js" v-model="blog.categories">
      </div>
      <label>作者：</label>
      <select  v-model="blog.author">
        <option v-for="author in authors" :key="author">{{author}}</option>
      </select>
      <button @click.prevent = "putBlogData">修改博客</button>
    </form>
    <div v-if="submited">
      <h3>恭喜你的博客已经提交成功了！🎉</h3>
    </div>

    <div class="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容:</p>
      <p>{{blog.content}}</p>
      <p>博客分类：</p>
      <ul>
       <li v-for="category in blog.categories" :key="category">
         {{category}}
       </li>
      </ul>
      <p>作者：{{blog.author}}</p>
    </div>

  </div>

</template>

<script>
import axios from 'axios'
export default {
  name: 'add-blog',
  data () {
    return {
        id:this.$route.params.id, // 获取地址栏id的参数
        blog:{},
        authors:["herry","mary","jack"],
        submited:false
    }
  },
  methods:{
    getBlogData:function(){
          //this.$http.get('/posts/'+this.id+'.json/')
          axios.get('/posts/'+this.id+'.json/')
          .then(res=>{
              this.blog = res.data;
          })
    },
    putBlogData:function(){
        axios.put('/posts/'+this.id+".json/",this.blog)
        .then((res)=>{
            this.submited = true;
      });
    }
  },
  created(){
      this.getBlogData();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.add-blog *{
  box-sizing: border-box;
}
.add-blog{
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}
label{
  display: block;
  margin: 20px 0 10px;
}
input[type="text"],select,textarea{
  width: 100%;
  display: block;
  padding: 10px;
}
textarea{
  height: 200px;
}
.checkboxes label{
  display: inline-block;
  margin-top: 0;
}
.checkboxes input{
  margin-right: 10px;
}
button{
  display: block;
  margin: 20px 0;
  background:crimson;
  color: #ffffff;
  border: 0;
  padding: 10px;
  font-size: 18px;
  border-radius: 4px;
  cursor: pointer;
}
.preview{
  padding: 20px 10px;
  border: 1px dotted #cccccc;
  margin: 30px 0;
}
h3{
  margin-top: 10px;
}
</style>



