import ShowBlogs from './components/ShowBlogs.vue'
import AddBlogs from './components/AddBlog.vue'
import SingBlogs from './components/SingBlogs.vue'
import EditBlogs from './components/EditBlogs.vue'

export default [
    {path:'/MyBlog/',component:ShowBlogs},
    {path:'/MyBlog/add',component:AddBlogs},
    {path:'/MyBlog/blog/:id',component:SingBlogs},// 路由传参数
    {path:'/MyBlog/edit/:id',component:EditBlogs},// 路由传参数

]


