<template>
    <div class='sing-blog'>
        <h1>{{blog.title}}</h1>
        <article>
            {{blog.content}}
        </article>
        <p>作者:{{blog.author}}</p>
        <p>分类:</p>
        <ul>
            <li v-for="item in blog.categories" :key="item">{{item}}</li>
        </ul>
        <button @click="deleteBlog">删除</button>
        <router-link :to="'/MyBlog/edit/' + id">编辑</router-link>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name:'sing-blog',
    data(){
        return{
            id:this.$route.params.id, // 获取地址栏id的参数
            blog:{}
        }
    },
    created:function(){
        // this.$http.get('https://vue-test-db-43a08.firebaseio.com/posts/'+this.id+'.json/')
        // .then(function(res){
        axios.get('/posts/'+this.id+'.json/')
        .then((res)=>{
            return res.data;
        }).then((data)=>{
            // console.log(data)
            this.blog = data;
        })
    },
    methods:{
        deleteBlog:function(){
            // this.$http.delete('https://vue-test-db-43a08.firebaseio.com/posts/'+this.id+'.json/')
            // .then(function(res){
            axios.delete('/posts/'+this.id+'.json/')
            .then((res)=>{
                this.$router.push({path:'/Myblog'})
            })
        }
    }
}
</script>
<style scoped>
.sing-blog{
    max-width: 960px;
    margin: 0 auto;
    padding: 20px;
    background: #eeeeee;
    border: 1px dotted #aaaaaa
}

</style>



